.quiz{
  background-image: url(https://cdn.pixabay.com/photo/2024/01/19/13/27/fog-8519076_640.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
 overflow: hidden;
}

.quiz-container {
  width: 500px;
  height: 200px;
   margin: 50px auto 50px auto; 
  margin-top: 220px;
  background-color:rgba(0, 0, 0, 0.486);
  border-radius: 10px;
  text-align: center;
  color: white;
  padding-top: 50px;
}

.question-section {
  margin-bottom: 20px;
}

.question-count {
  margin-bottom: 10px;
}

.answer-section button {
 margin: 5px;
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.answer-section button:hover {
  background-color: #0056b3;
}

.score-section {
  font-size: 24px;
}